  getGroupInfo() {
    let access = this.userData.designation
    let field = "id,group_name,is_hide,position,table_id,table_name," + access
    let getBody = { table_name: "meta_field_group", fields: field, limit: 20, page: 0, sort_type: "ASC", sort_field: "position", filter: [] }
    this.loadSave = 1;
    let temp = { table_id: this.routeInfo.id }
    getBody.filter.push(temp);
    this.CommonService.getValues(getBody).subscribe((response: any) => {
      if (response.statusCode == 200) {
        this.groupInfo = response.info;
        this.getMetaData();
      }
    });
  }

  workFlowChanges(item) {
    for (let i = 0; i < item.changed.length; i++) {
      if (item.changed[i].value_id != "") {
        this.values[item.changed[i].name] = item.changed[i].value_id;
        this.valuesTemp[item.changed[i].name] = item.changed[i].value_id;
        this.editValues[item.changed[i].name] = item.changed[i].value_id;
      } else {
        this.values[item.changed[i].name] = item.changed[i].value;
        this.valuesTemp[item.changed[i].name] = item.changed[i].value;
        this.editValues[item.changed[i].name] = item.changed[i].value;
      }

    }
    this.updateValues('save');
    this.updateSubValues()
  }

