  valueChanges(name, value) {
    this.saveCheck = 1
    this.values[name] = value;
    this.valuesTemp[name] = value;
    this.editValues[name] = value;
    // console.log("ddddddd", this.values, this.editValues)
    let temp = Object.keys(this.dateDifference)
    for (let i = 0; i < temp.length; i++) {
      if (this.dateDifference[temp[i]].includes(name)) {
        if (this.editValues[this.dateDifference[temp[i]][0]] !== "" && this.editValues[this.dateDifference[temp[i]][1]] !== "") {
          let date1: any = new Date(this.editValues[this.dateDifference[temp[i]][0]]);
          let date2: any = new Date(this.editValues[this.dateDifference[temp[i]][1]]);
          let diffTime = Math.abs(date1 - date2);
          let diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
          if (date1 < date2) {
            this.values[temp[i]] = diffDays + " " + this.dateDifference[temp[i]][2]?.delay_status
            this.valuesTemp[temp[i]] = diffDays + " " + this.dateDifference[temp[i]][2]?.delay_status
            this.editValues[temp[i]] = diffDays + " " + this.dateDifference[temp[i]][2]?.delay_status
          } else if (date1 > date2) {
            this.values[temp[i]] = diffDays + " " + this.dateDifference[temp[i]][2]?.early_status
            this.valuesTemp[temp[i]] = diffDays + " " + this.dateDifference[temp[i]][2]?.early_status
            this.editValues[temp[i]] = diffDays + " " + this.dateDifference[temp[i]][2]?.early_status
          } else {
            this.values[temp[i]] = this.dateDifference[temp[i]][2]?.on_time_status
            this.valuesTemp[temp[i]] = this.dateDifference[temp[i]][2]?.on_time_status
            this.editValues[temp[i]] = this.dateDifference[temp[i]][2]?.on_time_status
          }
        }
      }

    }
    this.relativeFunction();
  }

