  insideSubRow(parentTable, subTable) {

    if (!this.subViewData.hasOwnProperty(parentTable)) {
      this.subViewData[parentTable] = {}
    }
    if (!this.subViewData[parentTable].hasOwnProperty(subTable)) {
      this.subViewData[parentTable][subTable] = {};
    }
    if (!this.subViewData[parentTable][subTable].hasOwnProperty(this.insideSubIndex)) {
      this.subViewData[parentTable][subTable][this.insideSubIndex] = []
    }

    let temp = {};
    for (let i = 0; i < this.subViewTableInfo[subTable].length; i++) {
      temp[this.subViewTableInfo[subTable][i].name] = "";
    }
    temp["row_id"] = this.insideSubIndex;
    let t3 = [{ "row_id": this.insideSubIndex, "last_modified_by": this.userData.id, "created_by": this.userData.id }];
    this.CommonService.createSubValues(t3, subTable).subscribe((res: any) => {
      if (res.statusCode == 200) {
        temp["id"] = res.last_insert_id[0];
        this.subViewData[parentTable][subTable][this.insideSubIndex].push(temp);
      }
    });
  }

  insideSubRowAll(parentTable) {

    if (!this.subViewData.hasOwnProperty(parentTable)) {
      this.subViewData[parentTable] = []
    }

    let temp = {};
    for (let i = 0; i < this.subViewTableInfo[parentTable].length; i++) {
      temp[this.subViewTableInfo[parentTable][i].name] = "";
    }
    temp["row_id"] = this.insideSubIndex;
    let t3 = [{ "row_id": this.insideSubIndex, "last_modified_by": this.userData.id, "created_by": this.userData.id }];
    this.CommonService.createSubValues(t3, parentTable).subscribe((res: any) => {
      if (res.statusCode == 200) {
        temp["id"] = res.last_insert_id[0];
        this.subViewData[parentTable].push(temp);
      }
    });
  }

  insideSubValueChange(ind, parentTable, subTable, item, value, id) {
    // console.log("sssssssss", item, ind, parentTable, subTable, item, value, id)
    // console.log("Sssssss", this.subViewData, this.subViewTableInfoTable)

    let values = value;
    if (item.field_sub_type == "date") {
      let d = new Date(value),
        month = '' + (d.getMonth() + 1),
        day = '' + d.getDate(),
        year = d.getFullYear();
      if (month.length < 2)
        month = '0' + month;
      if (day.length < 2)
        day = '0' + day;
      values = [year, month, day].join('-');
    }

    let temp = { "id": id, [item.name]: value }
    this.CommonService.commonPut(temp, subTable).subscribe((res: any) => {
      if (res.statusCode == 200) {
        this.subViewData[parentTable][subTable][this.insideSubIndex][ind][item.name] = value;
      }
    });
  }

  insideSubValueChangeAll(ind, parentTable, item, value, id) {
    // console.log("sssssssss", item, ind, parentTable, item, value, id)
    let values = value;
    if (item.field_sub_type == "date") {
      let d = new Date(value),
        month = '' + (d.getMonth() + 1),
        day = '' + d.getDate(),
        year = d.getFullYear();
      if (month.length < 2)
        month = '0' + month;
      if (day.length < 2)
        day = '0' + day;
      values = [year, month, day].join('-');
    }

    let temp = { "id": id, [item.name]: value }
    this.CommonService.commonPut(temp, parentTable).subscribe((res: any) => {
      if (res.statusCode == 200) {
        this.subViewData[parentTable][ind][item.name] = value;
      }
    });
  }

  getDropdownValues(item, value) {
    let body = {
      "limit": 10,
      "page": 0,
      "db_name": item.db_name,
      "table_name": item.parent_table_name,
      "sort_type": "ASC",
      "sort_field": "id",
      "search": [
        { [item.parent_table_field]: value }
      ]
    }
    if (this.dependedField[item.name] && (this.editValues[this.dependedField[item.name]]?.value == "" || !this.values[this.dependedField[item.name]])) {
      this.depended[item.name] = 'empty';
    } else {
      this.depended[item.name] = 'true';
    }
    if (item.depended_id == "" || this.depended[item.name] == 'true') {
      if (this.dependedField[item.name]) {
        body["filter"] = [{
          [item.depended_id]: this.values[item.depended_field]
        }]
      }
      this.CommonService.getValues(body).subscribe((response: any) => {
        this.dropdownSearch = response.info;
      });
    }
  }

  getUserValues(item, value) {
    let body = {
      "limit": 10,
      "page": 0,
      "db_name": item.db_name,
      "table_name": "user",
      "sort_type": "ASC",
      "sort_field": "id",
      "search": [
        { "user_name": value }
      ]
    }
    if (this.dependedField[item.name] && (this.editValues[this.dependedField[item.name]]?.value == "" || !this.values[this.dependedField[item.name]])) {
      this.depended[item.name] = 'empty';
    } else {
      this.depended[item.name] = 'true';
    }
    if (item.depended_id == "" || this.depended[item.name] == 'true') {
      if (this.dependedField[item.name]) {
        body["filter"] = [{
          [item.depended_id]: this.values[item.depended_field]
        }]
      }
      this.CommonService.getValues(body).subscribe((response: any) => {
        if (response.statusCode == 200) {
          for (let i = 0; i < response.info.length; i++) {
            if (response.info[i].photo != "") {
              response.info[i].photo = JSON.parse(response.info[i].photo)
            }
          }
          this.userSearch = response.info;
        }
      });
    }
  }

  checkUniqueValues(item, value) {
    let body = {
      "limit": 10,
      "page": 0,
      "db_name": "tracking_db",
      "table_name": this.routeInfo.tablename,
      "sort_type": "ASC",
      "sort_field": "id",
      "filter": [
        { [item]: value }
      ]
    }
    this.CommonService.getValues(body).subscribe((response: any) => {
      if (!response.info.length) {
        this.valueChanges(item, value);
        this.uniqueField[item] = 0;
      } else {
        this.uniqueField[item] = 1;
        delete this.values[item];
      }
    });
  }

