  createSubFile(storeFile, item, id) {

    let temp = {};
    let myDate = new Date();
    temp["append_file"] = false;
    temp["file_data"] = storeFile.file_data;
    temp["file_name"] = storeFile.file_name + myDate;
    temp["file_extension"] = storeFile.file_extension;
    temp["table_name"] = item.table_name
    temp["field_id"] = id;
    temp["field_name"] = item.name;
    temp["file_display"] = storeFile.file_name;
    this.CommonService.createFile(temp).subscribe((response: any) => {
      if (response.status == 200) {
        this.subValues[this.subitem.table_name][this.subin][item.name] = []
        let temp2 = { "file_name": storeFile.file_name, "file_url": "https://tracking.airliftnet.com/" }
        this.subValues[this.subitem.table_name][this.subin][item.name].push(temp2)
      }
    });
  }

